.segsFrame {
  width: 480px;
  margin-left: calc((100vw - 480px - 320px - 56px) / 2);
  position: relative;
  .segsContent {
    box-sizing: border-box;
    width: 100%;
    padding: 16px;
    padding-bottom: 8px;
    background-color: #262626;
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    position: relative;

    div::selection {
      background-color: #2e8b61;
      color: #222222;
    }

    .chatSeg {
      background-color: #333333;
      color: #bbbbbb;
      width: fit-content;
      padding: 4px 10px;
      border-radius: 4px;
      margin-bottom: 8px;
      max-width: 320px;
    }
    .leftSeg {
      background-color: #bbbbbb;
      color: #222222;
    }
    .rightSeg {
      align-self: end;
    }
    .centerSeg {
      align-self: center;
      color: #666666;
      margin-bottom: 8px;
      max-width: 360px;
    }
    .emptySegsEnd {
      height: 16vh;
    }
    .noText {
      position: absolute;
      font-weight: bold;
      color: #444444;
      font-size: 32px;
      user-select: none;
      text-align: center;
      width: inherit;
      left: 0;
      top: calc(20vh + 48px);
    }
  }
  .topShade,
  .bottomShade {
    position: absolute;
    width: 100%;
    height: 4px;
    background-color: #237e54;
    animation-name: disappear;
    animation-duration: 0.4s;
    animation-fill-mode: forwards;
  }
  .topShade {
    top: 0;
  }
  .bottomShade {
    bottom: 0;
  }
  @keyframes disappear {
    0% {
      opacity: 1;
    }
    100% {
      opacity: 0;
      height: 0;
      display: none;
    }
  }
}

.panels {
  position: fixed;
  left: calc((100vw - 480px - 360px - 16px) / 2 + 480px + 56px);
  top: 20vh;

  .filePanel {
    box-sizing: border-box;
    width: 320px;
    background-color: #262626;
    padding: 16px;
    margin-bottom: 8px;
    cursor: default;

    .fileName {
      width: fit-content;
      &:hover {
        color: #888888;
        cursor: pointer;
      }
      &::selection {
        background-color: #2e8b61;
        color: #222222;
      }
      .fileInput {
        display: none;
      }
    }
    .saveButton {
      box-sizing: border-box;
      background-color: #237e54;
      border-radius: 4px;
      width: fit-content;
      padding: 0 10px;
      height: 32px;
      line-height: 32px;
      text-align: center;
      color: #cccccc;
      align-self: end;

      margin-top: 4px;
      user-select: none;

      &:hover {
        background-color: #146542;
        color: #888888;
        cursor: pointer;
      }
    }
  }

  .segTextInputPanel {
    box-sizing: border-box;
    width: 320px;
    background-color: #262626;
    padding: 16px;

    display: flex;
    flex-direction: column;
    .segTextInput {
      box-sizing: border-box;
      width: 100%;
      height: 80px;
      font-size: inherit;
      font-family: inherit;
      resize: none;
      outline: none;
      padding: 8px;
      background-color: #222222;
      color: #bbbbbb;
      border: 2px #444444 solid;
      border-radius: 4px;
      margin-bottom: 8px;

      &::selection {
        background-color: #2e8b61;
        color: #222222;
      }
      &:hover {
        border-color: #666666;
      }
    }
    .buttons {
      display: flex;
      justify-content: space-between;
      user-select: none;
      .whichSegToSendButtons {
        display: flex;
        .whichSegToSendButton {
          box-sizing: border-box;
          width: 32px;
          height: 32px;
          margin-right: 8px;
          text-align: center;
          line-height: 32px;
          border-radius: 4px;
          background-color: #333333;
          color: #666666;

          &.selected {
            background-color: #666666;
            color: #cccccc;
            &:hover {
              background-color: #666666;
              color: #cccccc;
              cursor: default;
            }
          }
          &:hover {
            background-color: #444444;
            color: #888888;
            cursor: pointer;
          }
        }
      }
      .sendButton {
        box-sizing: border-box;
        background-color: #237e54;
        border-radius: 4px;
        width: fit-content;
        padding: 0 10px;
        height: 32px;
        line-height: 32px;
        text-align: center;
        color: #cccccc;
        align-self: end;

        &:hover {
          background-color: #146542;
          color: #888888;
          cursor: pointer;
        }
      }
    }
  }

  .toTopAndToBottom {
    margin-top: 64px;
    padding-right: 40px;
    display: flex;
    justify-content: flex-end;
    user-select: none;
    .toTopAndToBottomIcon {
      margin-left: 8px;
      background-color: #262626;
      border-radius: 4px;
      width: 40px;
      height: 40px;
      color: #666666;
      line-height: 40px;
      text-align: center;
      &:hover {
        background-color: #333333;
        font-size: 24px;
        color: #237e54;
        cursor: pointer;
      }
    }
  }
}
