.test {
  width: 200px;
}

$width: 400px;
$padding: 0 8px;

.textareaFrame {
  box-sizing: border-box;
  width: $width;
  display: flex;
  height: fit-content;
  position: relative;

  margin: 0 auto;

  .what {
    box-sizing: border-box;
    width: $width;
    overflow-wrap: break-word;
    padding: $padding;
    z-index: -30;
    //visibility: hidden;
    white-space: pre-wrap;

    &.extraLine {
      padding-bottom: 22px;
    }
  }

  .textarea {
    box-sizing: border-box;
    padding: $padding;
    border: none;
    outline: none;
    background-color: #333333;
    width: $width;
    height: 100%;
    min-height: 22px;
    resize: none;
    font-family: inherit;
    color: #eeeeee;
    font-size: inherit;
    overflow-y: hidden;
    z-index: 10;
    position: absolute;
  }
}
